<template>
    <transition name="fade">
        <div v-if="show" class="preloader">
            <img src="../assets/img/zoo.png" alt="Vuemmerce Logo" class="logo">
            <p>Loading Vuemmerce Shop...</p>
            <div class="circles">
				<div class="circle"></div>
				<div class="circle"></div>
				<div class="circle"></div>
				<div class="circle"></div>
				<div class="circle"></div>
			</div>
        </div>  
    </transition>
</template>

<script>
export default {
    name: 'preloader',
    components: {
	},
	data(){
		return {
            show: true
		}
	},
	mounted(){
        if(this.show) this.showToggle()
	},
	methods: {
		showToggle(){
			setTimeout(() => {
                this.show = false;
                const path = '/Looks/LOOK1';
                if (this.$route.path !== path) this.$router.push(path);
			}, 1500)
		}
	}
}
</script>