<template>
<div>
    <div v-if="productSwitch">
        <img @click="RouteToProduct" :src="imgLinks[0]" class="card-img-top imgsmall imgspace center">
    </div>
    <VueSlickCarousel v-else :arrows="false" :dots="true">
        <img @click="RouteToProduct" v-for="imgLink in imgLinks" :key="imgLink" :src="imgLink" class="card-img-top center">
    </VueSlickCarousel>
</div>
</template>

<script>
import VueSlickCarousel from 'vue-slick-carousel'

export default {
    name: "productitem",
    props: ["productitem", "productitems", "productSwitch"],
    components: {
        VueSlickCarousel
    },
    data(){
        return {
            articleID: null,
            imgLinks: []
        }
    }, 
    created(){
        this.articleID = this.productitem.sku;
        this.imgLinks = this.productitem.photos;
    },
    methods: {
        RouteToProduct: function() {
            console.log("clicked image");
            const article = this.productitem;
            if (this.$route.path !== `/Product/${article.sku}`){
                console.log("emitting object");
                this.$emit('passArticle', article);
                return this.$router.push(`/Product/${article.sku}`);
            }
            return console.log("Already on path!");
        }
    }
}
</script>

