<template>
    <body>
        <div v-if="productSwitch" >
            <div class="card product-item fullscreen">
                <h1 class="text-center p-5">{{filterName}}</h1>
                <div class="wrapper">
                    <div v-if="showVid" class="look-video first-box">
                        <video id="videoElement" @ended="SwitchToPicture" @click="SwitchToPicture" :src="look.videos[0]" class="card-img-top vid-box center" autoplay="true"></video>
                    </div>
                    <div v-if="showPic" class="look-picture first-box">
                        <img @click="SwitchToVideo" :src="look.photos[0]" class="card-img-top img-box center">
                    </div>  
                    <div class="card-body second-box">
                        <ProductItem 
                            v-on:passArticle="getArticle($event)"
                            class="card-image center" 
                            v-bind:productitem="productitem" 
                            v-bind:key="productitem.sku"
                            v-bind:productSwitch="productSwitch" 
                            v-for="productitem in look.articles" 
                        />
                    </div>
                </div>    
                                
                <div class="card-body title-padding">
                    <h5 class="card-title">Look-description:</h5>
                    <p class="card-text description-padding"> {{look.description}} {{look.fit}} </p>
                    
                </div>
            </div> 
        </div>
        <div v-else>
            <div class="card product-item fullscreen">
                <!-- <h1 class="text-center p-5">{{article.name}}</h1> -->
                    <section class="tm-banner fullscreen">
                        <div class="tm-container-outer tm-banner-bg">
                            <div class="container">
                             <div class="row tm-banner-row tm-banner-row-header">
                                    <div class="col-xs-12">
                                        <div class="tm-banner-header">
                                            <h1 class="text-uppercase tm-banner-title">Veummerce Shop</h1>
                                            <img class="padding" src="../assets/img/dots-3.png" alt="Dots">
                                            <h4 class="tm-banner-subtitle ">{{article.name}}</h4>
                                        </div>    
                                    </div>  <!-- col-xs-12 -->                      
                                </div>
                                <div class="row tm-banner-row" id="tm-section-search">
                                    <form action="index.html" method="get" class="tm-search-form tm-section-pad-2 ">
                                        <div class="wrapper"> 
                                            <div class="look-picture2 first-box text-center">
                                                <ProductItem 
                                                    v-bind:productitem="article" 
                                                    v-bind:key="article.sku"
                                                />
                                            </div>  
                                            <div class="card-body second-box">
                                                <h5 class="card-title">Product-description:</h5>
                                                <p class="card-text description-padding"> {{article.description}} </p>
                                                <button @click="$router.go(-1)" type="button" class="btn btn-primary btn-lg btn-block">Back to the look</button>
                                            </div>
                                        </div>                       
                                    </form>                             
                                </div>
                            </div>               
                        </div>                
                    </section>
            </div>
        </div>
    </body>
</template>

<script>
import ProductItem from './ProductItem';

export default {
    name: "lookitem",
    props: ["look", "looks"],
    components: {
        ProductItem
    },
    data(){
        return {
            filterName: this.look.name,
            lookID: this.look.id,
            showVid: null,
            showPic: null,
            productSwitch: null,
            article: null
        }
    },
    created(){
        this.productSwitch = true;
        this.showVid = false;
        this.showPic = true;
    },
    mounted(){
        if(this.showPic) this.filterNumbers()
    },
    watch:{
        $route (){
            if (this.$route.path == `/Product/${this.article.sku}`){
                console.log("Watcher activated: Route changed to '/Products' !")
                this.productSwitch = false;
            } 
            else{
                this.productSwitch = true;
            }
            
        }
    },
	methods: {
        getArticle: function(passedarticle){
            console.log("getArticle function");
            this.article = passedarticle;
        },
		filterNumbers(){
            this.filterName = this.filterName.replace(/[0-9]/g, '');
        },
        SwitchToVideo(){
            this.showPic = false;
            this.showVid = true;
        },
        SwitchToPicture(){
            this.showVid = false;
            this.showPic = true;
        }
    }
}
</script>

