import Vue from 'vue'
import VueRouter from 'vue-router'
import Looks from '../views/Looks.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/Looks/:lookID', //'/Looks/:id'
    name: 'Looks',
    props: true,
    component: Looks
  },
  {
    path: '/Product/:articleID', //'/Product/:key'
    name: 'Looks',
    props: true,
    component: Looks
  }
]

const router = new VueRouter({
  routes
})

export default router
