<template>
    <body>
        <div class="products fullscreen">
            <!-- <VueSlickCarousel :arrows="true" :dots="true"> -->
                <div v-bind:key="look.id" v-for="look in looks">
                    <LookItem v-bind:look="look" v-bind:looks="looks"/>
                </div>
            <!-- </VueSlickCarousel> -->
        </div>
        <!-- footer -->
        <footer id="footer" class="bg-light">
            <ul class="copyright">
                <li v-if="buttonToggle" ><button @click="RouteToPrevious" type="button" class="btn btn-outline-dark">Previous</button></li>
                <li>&copy; The ZOO. All rights reserved.</li>
                <li v-if="buttonToggle"><button @click="RouteToNext" type="button" class="btn btn-outline-dark">Next</button></li>
            </ul>
        </footer>
    </body>
</template>

<script>
import LookItem from '../components/LookItem.vue';
// import VueSlickCarousel from 'vue-slick-carousel';

import axios from 'axios';

export default {
    name: 'Looks',
    props:["lookID", "articleID"],
    components: {
        LookItem
        // VueSlickCarousel
    },
    data(){
        return{
            looks: null,
            buttonToggle: null
        }
    },
    created(){
        //http request to the JSON server using a proxy made in Vue.config.file
        console.log("getting data");
        axios.get('/looks.json')
        .then(res => this.looks = res.data.looks)
        .catch(err => console.log(err));

        //Troubleshooting
        console.log("articleID:" + this.articleID);
        console.log("lookID:" + this.lookID);

        //defining variable states
        this.buttonToggle = true;
    },
    watch:{
        $route (){
            
            if (this.$route.path == `/Looks/${this.lookID}`){
                console.log("Watcher activated: Route changed to '/LOOKS' !")
                this.buttonToggle = true;
            } 
            else{
                this.buttonToggle = false;
            } 
        }
    },
    methods: {
        RouteToPrevious(){
            var objArr = this.looks;
            for(var i = 0; i < objArr.length; i++){
                if(objArr[i].id == this.lookID){
                    var index = i;
                }
                else{
                    index = -1;
                }    
            }

            var id = this.looks[index-1].id;
            console.log(id);

            const path = `/Looks/${id}`;
            console.log(path);
            
            if (this.$route.path !== path) this.$router.push(path);
		},
        RouteToNext(){
            var objArr = this.looks;
            for(var i = 0; i < objArr.length; i++){
                if(objArr[i].id == this.lookID){
                    var index = i;
                }
                else{
                    index = -1;
                }    
            }

            var id = this.looks[index+1].id;
            console.log(id);

            const path = `/Looks/${id}`;
            console.log(path);
            
            if (this.$route.path !== path) this.$router.push(path);
        }
    }
}
</script>

